.gpt3__blog {
  display: flex;
  flex-direction: column;
}

.gpt3__blog-heading h1 {
  width: 70%;
  margin-bottom: 7rem;
  font-family: var(--font-family);
  font-weight: 800;
  font-size: 62px;
  line-height: 75px;
  letter-spacing: -0.04em;
}

.gpt3__blog-container {
  display: flex;
  flex-direction: row;
}

.gpt3__blog-container-groupA {
  flex: 0.75;
  margin-right: 2rem;
}

.gpt3__blog-container-groupB {
  flex: 1;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 2rem;
}

@media screen and (max-width: 990px) {
  .gpt3__blog-container {
    flex-direction: column-reverse;
  }

  .gpt3__blog-container-groupA {
    margin: 2rem 0;
  }

  .gpt3__blog-container-groupA .gpt3__blog-container_article {
    width: 48%;
  }
}

@media screen and (max-width: 750px) {
  .gpt3__blog-container-groupB {
    grid-template-columns: repeat(1, 1fr);
  }

  .gpt3__blog-container-groupA .gpt3__blog-container_article {
    width: 100%;
  }

  .gpt3__blog-heading h1 {
      margin-top: -2rem;
    width: 100%;
    font-size: 35px;
    line-height: 50px;
    margin-bottom: 4rem;
  }
}
